/* */
export function schameUrl(param) {
    let schame = '';
    schame =  {
        "name": "openUrl",
        "params": {
            "url": encodeURI(param.url),
            "placeholder":param.img,
            "title":param.title,
            "docId":param.docId,
            "docType":param.docType
        }
    }
    return 'transfer://'+encodeURI(JSON.stringify(schame))
}

