import { invoke } from "./iframe";

let transfer = 'transfer://';
let nativeSchame = {
    schameUrl: function (param) {
        let schame = '';
        schame =  {
            "name": "openUrl",
            "params": {
                "url": encodeURI(param.url),
                "placeholder":param.img,
                "title":param.title,
                "docId":param.docId,
                "docType":param.docType
            }
        }
        return transfer + encodeURI(JSON.stringify(schame))
    },
    userLogin: function () {
        let schame =  {
                "name": "userLogin",
                "params": {
                },
                "callbackId": '__browser_userLogin_done'
            },
            src = transfer + encodeURI(JSON.stringify(schame))
        invoke("userLogin", src);
        window.__browser_userLogin_done = function (info) {
            window.__userLogin_getHeader_done = null;
            callback && callback(info);
        };

    },
    //
    getHeader: function (callback) {
        let schame =  {
            "name": "getHeader",
            "params": {
            },
            "callbackId": '__browser_getHeader_done'
        },
            src = transfer + encodeURI(JSON.stringify(schame));
        invoke("getHeader", src);
        window.__browser_getHeader_done = function (info) {
            window.__browser_getHeader_done = null;
            callback && callback(info);
        };
    },
    //
    encrypt: function (data,callback) {
        const encodeText = typeof data === "string" ? data : JSON.stringify(data);
        let schame =  {
                "name": "encrypt",
                "params": {
                    data:encodeText
                },
                "callbackId": '__browser_encrypt_done'
            },
            src = transfer + encodeURI(JSON.stringify(schame));
        invoke("encrypt", src);
        window.__browser_encrypt_done = function (info) {
            window.__browser_encrypt_done = null;
            callback && callback(info);
        };
    },
    //
    openNativePage: function (name='',type='') {
        let schame =  {
            "name": name || 'openHome',
            "params": {
                "nav":type,
            }
        }
        console.log(transfer + encodeURI(JSON.stringify(schame)))
        window.location.href = transfer + encodeURI(JSON.stringify(schame))
    },
    //H5
    openH5Page: function (url) {
        let schame =  {
            "name": 'openWeb',
            "params": {
                "url":url,
            }
        }
        window.location.href = transfer + encodeURI(JSON.stringify(schame))
    }
}

export default nativeSchame





