import {fetchData, getParams,printLog, getDocDate,getBrowserType,setCookie,getCookie} from '@/common/js/common.js'
import {getApi,getRemainTime,userLogin} from '../../app/source/main.js';
import native from '../../app/source/native';
export default {
    name:'Task',
    data(){
        return {
            nativeHeader:'',
            dataListR:[],
            dataSign:{
                list:[],
                days:0,
                sign:true
            },
            dataBox:{
                time:'',
                class:'',
                open:true,
                openCoin:0
            },
            showModal: {
                type:'',
                coin:''
            },
            isLogin:false,
            isApp:false,
            dataError:''
        }
    },
    props:{

    },
    watch:{
        dataError:function (newD) {
            console.log(newD)
        }
    },
    components:{

    },
    created: function () {
        let self = this;
        this.isApp = getBrowserType('HotNews') || 1;



        //self.getData();
        if(this.isApp){
            native.getHeader(function (data) {
                self.nativeHeader = data;
                if(data['User-T'] && data['User-T']!=''){
                    self.isLogin=true;
                }
                native.encrypt({
                    'timestamp':new Date().getTime()
                }, (back)=>{
                    self.getSignBox(back);
                    self.getData(back);
                })
            });
        }
    },
    methods:{
        //
        getData:function () {
            fetchData({
                method: 'get',
                url: getApi('task'),
                success: (data) => {
                    if(data.code==10000){
                        this.dataListR = data.data.rlist;
                        console.log(this.dataListR)
                    }
                },
                error:(err)=>{
                    this.dataError={
                        error:''
                    }
                },
                complete:(data)=>{
                    //console.log(data)
                }
            })
        },
        //
        getSignBox:function (param) {
            let self = this;
            fetchData({
                method: 'get',
                url: getApi('taskSignBox'),
                data:{
                    'data': param
                },
                headers:self.nativeHeader,
                success: (data) => {
                    console.log(data)
                    if(data.code==10000){
                        self.successFn(data.data)
                    }
                },
                error:(err)=>{
                    this.dataError={
                        error:''
                    }
                },
                complete:(data)=>{
                    //console.log(data)
                }
            })
        },

        //
        successFn:function (data) {
            let { signList, signCount, signFlag, lastOpenTime, time } = data;
            //
            let box_class='',
                box_time=0;
            if (time - lastOpenTime < 3600000) {
                // 
                const t = getRemainTime(time, lastOpenTime);
                const tf = i => ("") + i;
                const leftTime = `${tf(t.hours)}:${tf(t.minutes)}:${tf(t.seconds)}`;
                this.setTimer(time, lastOpenTime);
                box_class='wait';
                box_time=leftTime;
            }else{
                box_class='open';
            }
            this.dataBox = {
                time:box_time,
                class:box_class,
                open:box_class=='open',
                openCoin:0
            }
            //
            const countFlag = parseInt(signCount/7);
            signCount = signCount%7;
            //let goldCount = signCount >= 7 ? signList[6] : signList[signCount];
            this.dataSign={
                list:signList,
                days:signCount,
                sign:!signFlag
            }
        },


        //btn
        taskJump:function (target) {
            if(1 || this.isLogin){
                let dataset =target.target.dataset;
                let type = dataset.type;

                console.log(type)
                switch (type){
                    case 'accept_apprentice':           //
                        window.location.href = 'http://dev.3g.163.com:8081/xhh-invite.html#/'
                        native.openH5Page('task');
                        break;
                    case 'article':                     //
                        native.openNativePage('','hot');
                        break;
                    case 'share_article':               //
                        native.openNativePage('','hot');
                        break;
                    case 'photos':                      //
                        native.openNativePage('','pic');
                        break;
                    case 'minivideo':                   //
                        native.openNativePage('openShortVideo');
                        break;
                    case 'video':                   //
                        native.openNativePage('','video');
                        break;
                    default:
                        break;
                }
            }else{
                userLogin();
            }
        },

        /*  */
        signToday:function () {
            let signData = this.dataSign;
            console.log(this.isLogin+'====')
            if(!this.isLogin){
                userLogin();
                /*native.userLogin(()=>{
                    window.location.reload;
                });*/
                return;
            }
            if(signData.sign){
                //let coin =signData.list[signData.days];
                let self = this;
                let dataParam = {
                    'timestamp':new Date().getTime()
                };
                native.encrypt(dataParam, (back)=>{
                    fetchData({
                        method: 'POST',
                        url: getApi('taskSignCoin'),
                        data:{
                            data: back,
                        },
                        headers: self.nativeHeader,
                        success: (data) => {
                            console.log(data)
                            const signCount = parseInt((self.dataSign.days+1)/7);
                            if(data.code==10000){
                                self.dataSign.days = signCount%7;
                                self.dataSign.sign = !signData.sign;

                                self.showModal.type='sign'
                                self.showModal.coin=data.data.coin
                            }
                        },
                        error:(err)=>{
                            this.dataError={
                                error:''
                            }
                        },
                        complete:(data)=>{
                            //console.log(data)
                        }
                    })
                })

            }

        },
        /*  */
        openBox:function () {
            if (!this.isLogin) {
                userLogin();
                return;
            } else {
                let dataParam = {
                    'timestamp':new Date().getTime()
                };
                let self = this;
                native.encrypt(dataParam,(back)=>{
                    fetchData({
                        method: 'post',
                        url: getApi('taskOpenBox'),
                        data:{
                            data: back
                        },
                        headers: self.nativeHeader,
                        success: (data) => {
                            console.log(data)
                            if(data.code==10000){
                                let coin = data.data.coin,
                                    open_time = data.data.openTime;

                                self.showModal.type='box'
                                self.showModal.coin=coin;

                                self.dataBox = {
                                    time:open_time,
                                    class:'wait',
                                    open:false,
                                    openCoin:coin
                                }
                                self.setTimer(open_time, open_time);
                            }
                        },
                        error:(err)=>{
                            this.dataError={
                                error:''
                            }
                        },
                        complete:(data)=>{
                            //console.log(data)
                        }
                    })
                })

            }
        },

        /**
         * 
         * @param {} currentTime
         * @param {} lastTime
         * @param {} intervalTime
         */
        setTimer: function(currentTime, lastTime, intervalTime = 3600000) {
            let time = currentTime;
            let self = this;
            const intervalTimer = setInterval(() => {
                // 
                const remainTime = getRemainTime(time, lastTime, intervalTime);
                time += 1000;
                // 2
                if (remainTime.total <= intervalTime && remainTime.total > 0) {
                    const tf = i => ("") + i;
                    self.dataBox.time = `${tf(remainTime.hours)}:${tf(
                        remainTime.minutes
                    )}:${tf(remainTime.seconds)}`;
                } else if (remainTime.total <= 0) {
                    clearInterval(intervalTimer);
                    // 
                    self.dataBox.time = "";
                    self.dataBox.time = 'wait';
                }
            }, 1000);
        },
        setLogin:function () {
            native.userLogin(()=>{
                window.location.reload;
            })
        },
        closeModal:function () {
            this.showModal.type='';
        },
        // btn
        taskBtnJump: function (type) {
            console.log(type+'---')
        }
    }
}
window.handleConfirmFromNative = function() {
    //console.log('----handel')
}
window.__browser_encrypt_done = function(d) {
    console.log(d)
}


